<template>
  <div>
    <h1>{{ question }}</h1>
    <div id="image"><img v-show="img" :src="img" /></div>
    <ul>
      <li
        v-for="answer in answers"
        :key="answer.value"
        @click="handleClick(answer)"
      >
        {{ answer.value }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Question",
  data() {
    return {
      img: "",
    };
  },
  props: {
    question: String,
    answers: Array,
  },
  methods: {
    showImages(answer) {
      const images = answer.images;
      const _this = this;
      const currentImage = images.shift();
      if (!currentImage) {
        setTimeout(() => {
          _this.finishQuestion(answer);
        }, 500);
        return;
      }
      this.showImage(currentImage);
      setTimeout(() => {
        _this.showImages(answer);
      }, 500);
    },

    handleClick(answer) {
      this.showImages(answer);
    },
    finishQuestion(answer) {
      this.img = "";
      this.$emit("answered-question", answer.nextQuestion);
    },
    showImage(src) {
      try {
        this.img = require(`../assets/${src}`);
      } catch (e) {
        console.error(e);
      }
    },
  },
};
</script>

<style scoped>
ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 50%;
  margin: auto;
}

#image {
  display: flex;
  justify-content: center;
}

img {
  display: block;
}

h1 {
  font-size: 5rem;
  text-align: center;
}

li {
  font-size: 4rem;
  cursor: pointer;
}
</style>
