<template>
  <div>
    <Question
      v-show="!quizEndedBadly && !good"
      @answered-question="handleAnsweredQuestion"
      :question="currentQuestion.question"
      :answers="currentQuestion.answers"
    />
    <template v-if="good || quizEndedBadly">
      <h2>
        Tuviste {{ goodAnswers }} de {{ totalAnswers }} correctas. ({{
          this.percentage * 100
        }}%)
      </h2>
    </template>

    <template v-if="quizEndedBadly">
      <h2>No eres baby linda shi lo lamentamos</h2>
      <img src="../assets/brokenheart.png" />
    </template>
    <template v-if="good">
      <h2>Quieres ser mi valentines? :)</h2>
      <img src="../assets/heart.png" />
    </template>

    <template v-if="good || quizEndedBadly">
      <div>
        <button id="reset-btn" @click="handleReset">Reset</button>
      </div>
    </template>
  </div>
</template>

<script>
import Question from "@/components/Question";
export default {
  name: "Quiz",
  data() {
    return {
      currentQuestion: null,
      totalAnswers: 0,
      goodAnswers: 0,
      quizEndedBadly: false,
      good: false,
    };
  },
  beforeMount() {
    this.totalAnswers = this.questions.length;
    this.currentQuestion = this.questions.shift();
  },
  props: {
    questions: Array,
  },
  components: {
    Question,
  },
  methods: {
    handleAnsweredQuestion(nextQuestion) {
      if (nextQuestion) {
        this.goodAnswers++;
      }
      const next = this.questions.shift();

      if (!next) {
        this.good = this.passedQuiz;
        this.quizEndedBadly = !this.passedQuiz;
        return;
      }
      this.currentQuestion = next;
    },
    handleReset() {
      this.$emit("reset-quiz");
    },
  },
  computed: {
    percentage() {
      return this.goodAnswers / this.totalAnswers;
    },
    passedQuiz() {
      return this.percentage > 0.69;
    },
  },
};
</script>

<style scoped>
button {
  border: 0;
  padding: 0;
}

button#reset-btn {
  background-color: red;
  border-radius: 1rem;
  padding: 2rem;
  color: white;
  cursor: pointer;
}
</style>
